
/*========================================
 *  Title classes and styles
 *  [title sizing with incremental numbered growth like default HTML]
 *========================================*/

.h {
  font-family: $font-title;
  font-weight: 400;
  @include font-smoothing;
  &--xxxxl { font-size: $s7; line-height: 1.15; }
  &--xxxl  { font-size: $s7; line-height: 1.15; }
  &--xxl   { font-size: $s6;  line-height: 1.2; }
  &--xl    { font-size: $s4;  line-height: 1.3; }
  &--lg    { font-size: $s3;  line-height: 1.36; }
  &--md    { font-size: $s2;  line-height: 1.42; }
  &--sm    { font-size: $s1;  line-height: 1.45; }
  &--xs    { font-size: $s0;  line-height: 1.45; }
  &--xxs   { font-size: $s0;  line-height: 1.45; }
  @include bp($xl){
    &--xxxxl { font-size: $s9; line-height: 1.1; }
    &--xxxl  { font-size: $s8; line-height: 1.1; }
    &--xxl   { font-size: $s7; line-height: 1.15; }
    &--xl    { font-size: $s6; line-height: 1.2; }
    &--lg    { font-size: $s5; line-height: 1.32; }
    &--md    { font-size: $s4; line-height: 1.32; }
    &--sm    { font-size: $s3; line-height: 1.32; }
    &--xs    { font-size: $s2; line-height: 1.32; }
    &--xxs   { font-size: $s1; line-height: 1.38; }
  }

  // pre-styled
  &--subtitle {
    font-family: $font-title;
    font-weight: 600;
    letter-spacing: 0.15em;
    text-transform: uppercase;
  }
  &--banner {
    font-family: $font-title;
    font-weight: 600;
    font-size: $s8;
    line-height: 1.1;
    @include bp($lg){
      font-size: $s10;
    }
  }
}
